import { ReactNode, forwardRef, FunctionComponent, PropsWithoutRef, Ref } from 'react';
import styled, { css } from 'styled-components';

import {
  Grid,
  FieldValueList,
  StyledFieldValueList,
  StyledFieldValue,
  calculateFontSize,
  defaultThemeProp,
  ForwardProps,
  FontSize
} from '@pega/cosmos-react-core';

export const StyledCaseSummaryFields = styled.div(({ theme }) => {
  const fontSizes = calculateFontSize(theme.base['font-size'], theme.base['font-scale']);

  return css`
    padding: calc(2 * ${theme.base.spacing});

    > ${StyledFieldValueList}:first-child {
      grid-template-columns: 16ch auto;

      ${StyledFieldValue} {
        font-size: ${fontSizes[theme.components.text.h1['font-size'] as FontSize]};
        font-weight: ${theme.components.text.h1['font-weight']};
      }
    }
  `;
});

StyledCaseSummaryFields.defaultProps = defaultThemeProp;

export interface CaseSummaryFieldsProps {
  /** The primary summary fields to display above secondary fields. */
  primaryFields: {
    id?: string;
    name: string;
    value: ReactNode;
  }[];
  /** The secondary summary fields to display below primary fields. */
  secondaryFields: {
    id?: string;
    name: string;
    value: ReactNode;
  }[];
  /** Ref for the wrapping element. */
  ref?: Ref<HTMLDivElement>;
}

const CaseSummaryFields: FunctionComponent<CaseSummaryFieldsProps & ForwardProps> = forwardRef(
  (
    { primaryFields, secondaryFields, ...restProps }: PropsWithoutRef<CaseSummaryFieldsProps>,
    ref: CaseSummaryFieldsProps['ref']
  ) => {
    return (
      <Grid {...restProps} as={StyledCaseSummaryFields} ref={ref} container={{ rowGap: 2 }}>
        <FieldValueList variant='stacked' fields={primaryFields} />
        <FieldValueList variant='inline' fields={secondaryFields} />
      </Grid>
    );
  }
);

export default CaseSummaryFields;
