import {
  ReactNode,
  FC,
  MouseEvent,
  KeyboardEvent,
  useContext,
  useEffect,
  useState,
  useCallback,
  useMemo,
  useRef,
  useLayoutEffect
} from 'react';

import {
  Button,
  Flex,
  Grid,
  Icon,
  registerIcon,
  TabPanel,
  useBreakpoint,
  useScrollStick,
  useElement,
  ForwardProps,
  Card,
  Drawer,
  useI18n,
  useDirection,
  Tabs,
  TabsProps
} from '@pega/cosmos-react-core';
import { ScrollStickOptions } from '@pega/cosmos-react-core/lib/hooks/useScrollStick';
import AppShellContext from '@pega/cosmos-react-core/lib/components/AppShell/AppShellContext';
import * as moveRightSolidIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/move-right-solid.icon';
import * as moveLeftSolidIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/move-left-solid.icon';

import CaseSummary, { CaseSummaryProps } from '../CaseSummary';

import UtilitiesSummary, { UtilitiesSummaryProps } from './UtilitiesSummary';
import {
  StyledCaseDrawer,
  StyledCaseDrawerContent,
  StyledCaseView,
  StyledCaseViewContent,
  StyledPersistentUtility,
  StyledSummaryRegion,
  StyledCaseViewUtilities,
  StyledUtilToggle
} from './CaseView.styles';

registerIcon(moveRightSolidIcon, moveLeftSolidIcon);

export interface CaseViewProps extends CaseSummaryProps {
  /** A region to hold a Stages component. */
  stages?: ReactNode;
  /** A region to hold a Tasks component. */
  tasks?: ReactNode;
  /** A region that is used to hold Utility components for example SummaryList. */
  utilities?: ReactNode;
  /** The utilities summary array will be used to render the minimized the utilities card. */
  utilitiesSummaryItems?: UtilitiesSummaryProps['items'];
  /** Collapses the utilities by default on load */
  defaultCollapsedUtilities?: boolean;
  /** A region above the center column to display banners. */
  banners?: ReactNode;
  /** A region to the right reserved for persistent utilities. */
  persistentUtility?: { content: ReactNode; title: string };
  /** A region within the center column that is controlled by tabs in CaseSummary. */
  tabContent?: { content: ReactNode; id: string }[];
}

const CaseView: FC<CaseViewProps & ForwardProps> = (props: CaseViewProps) => {
  const {
    caseId,
    parentCases,
    heading,
    caseLink,
    icon,
    followed,
    onFollowedChange,
    summary,
    tabs: { currentTabId, items: tabItemsProp, onClick: onTabClick },
    stages,
    tasks,
    tabContent: tabContentProp = [],
    utilities,
    utilitiesSummaryItems,
    defaultCollapsedUtilities = false,
    actions,
    onEdit,
    banners,
    persistentUtility,
    forceMobile,
    ...restProps
  } = props;
  const t = useI18n();
  const caseViewRef = useRef<HTMLDivElement>(null);
  const caseViewSummaryRef: CaseSummaryProps['ref'] = useRef(null);
  const caseSummaryHeaderRef = useRef<HTMLDivElement>(null);
  const caseViewSummaryContainerRef = useRef<HTMLDivElement>(null);
  const [tabContentEl, setTabContentEl] = useElement<HTMLElement>(null);
  let isExtraLargeOrAbove = useBreakpoint('xl', { breakpointRef: caseViewRef });
  let isLargeOrAbove = useBreakpoint('lg', { breakpointRef: caseViewRef });
  let isMediumOrAbove = useBreakpoint('md', { breakpointRef: caseViewRef });
  if (forceMobile) {
    isExtraLargeOrAbove = false;
    isLargeOrAbove = false;
    isMediumOrAbove = false;
  }

  const { headerEl } = useContext(AppShellContext);

  const [summaryAnimationState, setSummaryAnimationState] = useState('expanded');

  const summaryTabId = `summary-${caseId}`;
  const utilitiesTabId = `utilities-${caseId}`;
  const persistentUtilityTabId = `persistent-utility-${caseId}`;
  const [utilitiesShown, setUtilitiesShown] = useState(
    isLargeOrAbove && !defaultCollapsedUtilities
  );
  const initialUserUtilState = defaultCollapsedUtilities ? 'hidden' : undefined;
  const [userUtilState, setUserUtilState] = useState<undefined | 'shown' | 'hidden'>(
    initialUserUtilState
  );
  const [summaryPanelCollapsed, setSummaryPanelCollapsed] = useState(false);

  const hasSideBySideColumns = isMediumOrAbove;
  const scrollStickOptions = useRef<ScrollStickOptions & { elements: HTMLElement[] }>({
    elements: []
  });

  const getOffsets = () => {
    const appshellHeaderHeight = headerEl?.offsetHeight || 0;

    const summaryHeight =
      !isMediumOrAbove || (persistentUtility && !isExtraLargeOrAbove) || summaryPanelCollapsed
        ? caseViewSummaryRef?.current?.offsetHeight || 0
        : 0;

    return {
      sticky: appshellHeaderHeight + summaryHeight,
      summary: summaryHeight
    };
  };

  const [offsets, setOffsets] = useState(getOffsets);

  useScrollStick(
    hasSideBySideColumns ? { ...scrollStickOptions.current, offset: offsets.sticky } : undefined
  );

  const toggleUtilities = useCallback((e: MouseEvent | KeyboardEvent) => {
    if (e.type === 'keydown' && (e as KeyboardEvent).key !== 'Enter') {
      return;
    }

    setUtilitiesShown(shown => {
      setUserUtilState(shown ? 'hidden' : 'shown');
      return !shown;
    });
  }, []);

  useLayoutEffect(() => {
    setOffsets(getOffsets());
  }, [isExtraLargeOrAbove, isLargeOrAbove, isMediumOrAbove, headerEl, summaryPanelCollapsed]);

  useEffect(() => {
    if (isLargeOrAbove) {
      setUtilitiesShown(userUtilState !== 'hidden');
    } else {
      setUtilitiesShown(false);
    }
  }, [isLargeOrAbove]);

  useEffect(() => {
    if (
      isMediumOrAbove &&
      !summaryPanelCollapsed &&
      !persistentUtility &&
      (currentTabId === summaryTabId || currentTabId === utilitiesTabId)
    ) {
      onTabClick(tabItemsProp[0]?.id);
    }
  }, [
    isMediumOrAbove,
    onTabClick,
    currentTabId,
    summaryTabId,
    utilitiesTabId,
    summaryPanelCollapsed
  ]);

  useEffect(() => {
    if ((isMediumOrAbove && !isLargeOrAbove && utilities) || persistentUtility)
      setUtilitiesShown(false);
  }, [isMediumOrAbove, utilities]);

  useEffect(() => {
    if (persistentUtility && currentTabId === persistentUtilityTabId) onTabClick(summaryTabId);
  }, [persistentUtility]);

  const tabItems: TabsProps['tabs'] = useMemo(() => {
    const summaryTab = { id: summaryTabId, name: 'Summary' };
    if (isMediumOrAbove)
      return summaryPanelCollapsed || (persistentUtility && !isExtraLargeOrAbove)
        ? [summaryTab, ...tabItemsProp]
        : tabItemsProp;

    const utilityTabs = [];

    if (utilities) utilityTabs.push({ id: utilitiesTabId, name: 'Utilities' });
    if (persistentUtility && !isMediumOrAbove)
      utilityTabs.push({ id: persistentUtilityTabId, name: persistentUtility.title });

    return [summaryTab, ...tabItemsProp, ...utilityTabs];
  }, [
    isMediumOrAbove,
    isExtraLargeOrAbove,
    summaryTabId,
    utilitiesTabId,
    persistentUtilityTabId,
    utilities,
    persistentUtility,
    tabItemsProp,
    summaryPanelCollapsed
  ]);

  useEffect(() => {
    if (!tabContentEl) return;
    const scrollPos = window.scrollY;
    const windowHeight = document.documentElement.clientHeight;
    const bottomPos = scrollPos + windowHeight * 0.8;
    const tabContentPos = tabContentEl.offsetTop;
    if (tabContentPos > bottomPos) {
      const top = tabContentPos - windowHeight * 0.2;
      window.scrollTo({ top, behavior: 'smooth' });
    }
  }, [currentTabId]);

  const tabContent = useMemo(() => {
    const summaryTabContent = { id: summaryTabId, content: <Card>{summary}</Card> };
    if (isMediumOrAbove)
      return summaryPanelCollapsed || (persistentUtility && !isExtraLargeOrAbove)
        ? [summaryTabContent, ...tabContentProp]
        : tabContentProp;

    const utilityTabs = [];
    if (utilities)
      utilityTabs.push({
        id: utilitiesTabId,
        content: <Flex container={{ direction: 'column', gap: 2 }}>{utilities}</Flex>
      });
    if (persistentUtility)
      utilityTabs.push({ id: persistentUtilityTabId, content: persistentUtility?.content });
    return [summaryTabContent, ...tabContentProp, ...utilityTabs];
  }, [
    currentTabId,
    tabContentProp,
    summary,
    summaryTabId,
    utilities,
    utilitiesTabId,
    isMediumOrAbove,
    isExtraLargeOrAbove
  ]);

  const direction = useDirection();

  const utilitiesIcon = utilitiesShown
    ? `move-${direction.end}-solid`
    : `move-${direction.start}-solid`;

  let caseViewGridContainer = {
    cols: 'minmax(0, 1fr)',
    colGap: 0,
    rows: 'minmax(0, max-content) auto',
    areas: '"case-nav" "content"'
  };
  let fixedSummaryWidth = '100%';

  if (!forceMobile && !persistentUtility && !summaryPanelCollapsed) {
    fixedSummaryWidth = isLargeOrAbove ? '25rem' : '22rem';
    if (isLargeOrAbove || isMediumOrAbove) {
      caseViewGridContainer = {
        cols: 'minmax(0, max-content) minmax(0, 1fr) minmax(0, max-content)',
        colGap: 2,
        rows: caseViewGridContainer.rows,
        areas: '"case-nav content utilities"'
      };
    }
  } else if (summaryPanelCollapsed) {
    if (isLargeOrAbove || isMediumOrAbove) {
      caseViewGridContainer = {
        cols: 'minmax(0, 1fr) minmax(0, max-content)',
        colGap: 2,
        rows: caseViewGridContainer.rows,
        areas: '"case-nav case-nav" "content utilities"'
      };
    }
  } else if (persistentUtility) {
    if (isExtraLargeOrAbove) {
      fixedSummaryWidth = '25rem';
      caseViewGridContainer = {
        cols: 'minmax(0, max-content) minmax(0, 1fr) repeat(2, minmax(0, max-content))',
        colGap: 2,
        rows: caseViewGridContainer.rows,
        areas: '"case-nav content persistentUtility utilities"'
      };
    } else if (isLargeOrAbove || isMediumOrAbove) {
      caseViewGridContainer = {
        cols: 'minmax(0, 1fr) repeat(2, minmax(0, max-content))',
        colGap: 2,
        rows: caseViewGridContainer.rows,
        areas: '"case-nav case-nav case-nav" "content persistentUtility utilities"'
      };
    }
  }

  const animateSummaryPanel = (type: 'open' | 'close') => {
    if (type === 'close') {
      const animation = [{ width: fixedSummaryWidth }, { width: 0 }];
      caseViewSummaryContainerRef.current?.animate(animation, 250);
    } else {
      const animation = [{ width: 0 }, { width: isLargeOrAbove ? '25rem' : '22rem' }];
      caseViewSummaryContainerRef.current?.animate(animation, 250);
    }
  };

  return (
    <Grid
      as={StyledCaseView}
      stickyOffset={offsets.sticky}
      ref={caseViewRef}
      container={caseViewGridContainer}
      item={{ area: 'caseView' }}
      forceMobile={forceMobile}
      isMediumOrAbove={isMediumOrAbove}
      {...restProps}
    >
      <Grid
        as={StyledSummaryRegion}
        item={{ area: 'case-nav' }}
        forceMobile={forceMobile}
        desktop={
          isMediumOrAbove && (!persistentUtility || isExtraLargeOrAbove) && !summaryPanelCollapsed
        }
        isLargeOrAbove={isLargeOrAbove}
        ref={caseViewSummaryContainerRef}
        style={
          ['collapsing', 'expanding'].includes(summaryAnimationState)
            ? {
                overflow: 'hidden'
              }
            : undefined
        }
      >
        <CaseSummary
          ref={caseViewSummaryRef}
          headerRef={caseSummaryHeaderRef}
          caseId={caseId}
          heading={heading}
          caseLink={caseLink}
          icon={icon}
          followed={followed}
          onFollowedChange={onFollowedChange}
          parentCases={parentCases}
          summary={summary}
          actions={actions}
          onEdit={onEdit}
          tabs={{
            items: tabItems,
            onClick: onTabClick,
            currentTabId
          }}
          forceMobile={forceMobile}
          desktop={
            ((isMediumOrAbove && !persistentUtility) || isExtraLargeOrAbove) &&
            !summaryPanelCollapsed
          }
          fixedWidth={isMediumOrAbove ? fixedSummaryWidth : undefined}
          collapsible={isMediumOrAbove && !persistentUtility}
          collapsed={summaryPanelCollapsed}
          onBeforeCollapse={() => {
            animateSummaryPanel('close');
          }}
          onAfterCollapse={() => setSummaryPanelCollapsed(true)}
          onBeforeExpand={() => {
            animateSummaryPanel('open');
            setSummaryPanelCollapsed(false);
          }}
          onAnimationStateChange={setSummaryAnimationState}
        />
      </Grid>

      <Flex
        container={{
          direction: 'column',
          alignContent: 'start',
          rowGap: 2
        }}
        as={StyledCaseViewContent}
        forceMobile={forceMobile}
        isMediumOrAbove={isMediumOrAbove}
        persistentUtility={Boolean(persistentUtility)}
        style={{
          transform: ['collapsing', 'expanding'].includes(summaryAnimationState)
            ? `translate(0, ${caseSummaryHeaderRef.current?.offsetHeight}px) `
            : undefined,
          marginInlineStart:
            (summaryPanelCollapsed && isMediumOrAbove) ||
            (persistentUtility && !isExtraLargeOrAbove)
              ? '1rem'
              : 0
        }}
      >
        {banners && <div>{banners}</div>}
        {stages && <div>{stages}</div>}
        {tasks && <div>{tasks}</div>}
        {(!isMediumOrAbove ||
          (persistentUtility && !isExtraLargeOrAbove) ||
          summaryPanelCollapsed) && (
          <Tabs tabs={tabItems} onTabClick={onTabClick} currentTabId={currentTabId} />
        )}
        <div ref={setTabContentEl}>
          {tabContent?.map(({ id, content }) => (
            <TabPanel currentTabId={currentTabId} tabId={id} key={id}>
              {content}
            </TabPanel>
          ))}
        </div>
      </Flex>

      {persistentUtility && isMediumOrAbove && (
        <Grid as={StyledPersistentUtility} item={{ area: 'persistentUtility' }}>
          {persistentUtility.content}
        </Grid>
      )}
      {!isLargeOrAbove && (
        <Drawer
          as={StyledCaseDrawer}
          open={utilitiesShown && isMediumOrAbove}
          position='fixed'
          placement={direction.end}
          shadow
          nullWhenClosed
          hasPersistentUtility={!!persistentUtility}
        >
          <Flex as={StyledCaseDrawerContent} container={{ direction: 'column', gap: 2 }}>
            <Flex container={{ justify: 'end' }}>
              <Button
                variant='simple'
                icon
                onClick={toggleUtilities}
                as={StyledUtilToggle}
                label={t('collapse_utilities_panel')}
              >
                <Icon name={`move-${direction.end}-solid`} />
              </Button>
            </Flex>
            {utilities}
          </Flex>
        </Drawer>
      )}
      {utilities && isMediumOrAbove && (
        <Grid
          ref={(el: HTMLDivElement) => {
            if (el) (scrollStickOptions.current?.elements).push(el);
          }}
          as={StyledCaseViewUtilities}
          container={{
            cols: 'minmax(0, 1fr)',
            rowGap: 2
          }}
          item={{
            area: 'utilities'
          }}
          utilitiesShown={utilitiesShown && isLargeOrAbove}
          style={
            ['collapsing', 'expanding'].includes(summaryAnimationState)
              ? {
                  transform: `translate(0, ${caseSummaryHeaderRef.current?.offsetHeight}px) `
                }
              : undefined
          }
        >
          <Flex container={{ justify: utilitiesShown ? 'end' : 'center' }}>
            <Button
              variant='simple'
              icon
              onClick={toggleUtilities}
              as={StyledUtilToggle}
              label={t(utilitiesShown ? 'collapse_utilities_panel' : 'expand_utilities_panel')}
            >
              <Icon name={utilitiesIcon} />
            </Button>
          </Flex>
          <Flex
            container={
              utilitiesShown && isLargeOrAbove ? { direction: 'column', gap: 2 } : undefined
            }
            hidden={!utilitiesShown || !isLargeOrAbove}
          >
            {utilities}
          </Flex>

          {(!utilitiesShown || (isMediumOrAbove && !isLargeOrAbove)) && (
            <UtilitiesSummary
              items={utilitiesSummaryItems || []}
              onClick={toggleUtilities}
              onKeyDown={toggleUtilities}
            />
          )}
        </Grid>
      )}
    </Grid>
  );
};

export default CaseView;
