import { FunctionComponent, useState, useRef, useEffect, useContext, useCallback } from 'react';
import { remToPx } from 'polished';

import {
  Icon,
  registerIcon,
  Flex,
  ForwardProps,
  useElement,
  useOuterEvent,
  Popover,
  useI18n,
  Button,
  ModalManagerContext,
  ModalProps,
  Modal,
  getFocusables,
  useBreakpoint,
  debounce,
  useTheme
} from '@pega/cosmos-react-core';
import * as checkIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/check.icon';

import { StagesProps } from './Stages.types';
import StepsContainer from './StepsContainer';
import { StyledInnerStage, StyledStage, StyledStagePopover, StyledStages } from './Stages.styles';

registerIcon(checkIcon);

const Stages: FunctionComponent<StagesProps & ForwardProps> = (props: StagesProps) => {
  const { current, stages, caseTitle, ...restProps } = props;
  const t = useI18n();

  const [stagesEl, setStagesEl] = useElement<HTMLDivElement>();
  const [currentStageNameEl, setCurrentStageNameEl] = useElement<HTMLSpanElement>();
  const [currentStageButtonEl, setCurrentStageButtonEl] = useElement<HTMLButtonElement>();

  const [activeStageIndex, setActiveStageIndex] = useState<number>(0);
  const activeStageObj = stages[activeStageIndex];
  const stagesRefs = useRef<HTMLElement[]>([]);
  const activeStageEl = stagesRefs.current[activeStageIndex];
  const { create } = useContext(ModalManagerContext);
  const [popoverEl, setPopoverRef] = useElement<HTMLElement>();
  const [showPopover, setShowPopover] = useState(false);
  const popoverContentRef = useRef(null);
  const isSmallOrAbove = useBreakpoint('sm');
  const focusableElements = getFocusables(popoverContentRef);

  const [compressedView, setCompressedView] = useState(false);
  const [minExpandedWidth, setMinExpandedWidth] = useState(0);

  const resizeObserver = new ResizeObserver(
    debounce((entries: ResizeObserverEntry[]) => {
      if (entries[0].contentRect.width > minExpandedWidth) setCompressedView(false);
    }, 100)
  );

  const theme = useTheme();

  const intersectionObserver = new IntersectionObserver(
    entries => {
      if (entries[0].intersectionRatio < 1) {
        if (stagesEl) setMinExpandedWidth(Math.max(minExpandedWidth, stagesEl.offsetWidth + 1));
        setCompressedView(true);
      }
    },
    { root: currentStageButtonEl, rootMargin: `0px -${remToPx(theme.base.spacing)}`, threshold: 1 }
  );

  useEffect(() => {
    if (stagesEl && currentStageNameEl) {
      intersectionObserver.observe(currentStageNameEl as Element);
      resizeObserver.observe(stagesEl);

      return () => {
        intersectionObserver.disconnect();
        resizeObserver.disconnect();
      };
    }
  }, [stagesEl, currentStageNameEl, compressedView]);

  useOuterEvent('mousedown', [popoverEl, activeStageEl], e => {
    if (showPopover) {
      setShowPopover(false);
      e.preventDefault();
    }
  });

  const onMouseDown = (ev: React.MouseEvent<HTMLElement>) => {
    if (!showPopover) ev.preventDefault();
  };

  const onKeyDown = useCallback(
    ({ key }: KeyboardEvent) => {
      if (key === 'Escape') {
        setShowPopover(false);
        activeStageEl?.focus();
      }
    },
    [activeStageEl]
  );

  const onKeyDownPopover = useCallback(
    (event: KeyboardEvent) => {
      if (event.key === 'Tab' && event.shiftKey) {
        const firstFocusable = focusableElements[0];

        if (document.activeElement === firstFocusable) {
          stagesRefs.current[activeStageIndex + 1]?.focus();
          setShowPopover(false);
        }
      }

      if (event.key === 'Tab' && !event.shiftKey) {
        const lastFocusable = focusableElements[focusableElements.length - 1];

        if (document.activeElement === lastFocusable) {
          activeStageEl?.focus();
          setShowPopover(false);
        }
      }
    },
    [activeStageEl, focusableElements, activeStageIndex]
  );

  useEffect(() => {
    document.addEventListener('keydown', onKeyDown);
    return () => {
      document.removeEventListener('keydown', onKeyDown);
    };
  }, [onKeyDown]);

  useEffect(() => {
    if (focusableElements.length) focusableElements[0].focus();
  }, [popoverEl]);

  const [requestDismiss, setRequestDismiss] = useState(false);

  useEffect(() => {
    if (requestDismiss) {
      setShowPopover(false);
      activeStageEl?.focus();
      setRequestDismiss(false);
    }
  }, [activeStageEl, requestDismiss]);

  const RenderModal = ({
    heading = t('lifecycle', [caseTitle || 'Case'])
  }: Partial<ModalProps>) => {
    return (
      <Modal {...restProps} heading={heading} onRequestDismiss={() => setRequestDismiss(true)}>
        {stages.map((stage, id) => (
          <StepsContainer currentStage={[stage]} key={id.toString()} />
        ))}
      </Modal>
    );
  };

  const openModal = () => {
    setShowPopover(false);
    create(RenderModal);
  };

  const isCurrent = (stageId: string) => current === stageId;

  return (
    <>
      <Flex container as={StyledStages} ref={setStagesEl} {...restProps}>
        {stages.map((stage, index) => (
          <Flex
            container={{ justify: 'center', alignItems: 'center' }}
            as={StyledStage}
            item={{ grow: compressedView && !isCurrent(stage.id) ? 0 : 1, shrink: 1 }}
            aria-current={isCurrent(stage.id) ? 'step' : undefined}
            aria-label={`Stage ${stage.name}`}
            key={`stage-${stage.id}`}
            ref={(el: HTMLButtonElement) => {
              stagesRefs.current[index] = el;
              if (isCurrent(stage.id)) setCurrentStageButtonEl(el);
            }}
            onMouseDown={onMouseDown}
            onClick={() => {
              setActiveStageIndex(index);
              if (isSmallOrAbove) {
                setShowPopover(!showPopover);
              } else {
                openModal();
              }
            }}
          >
            {stage.complete && <Icon name='check' />}
            {!(stage.complete && compressedView) && (
              <StyledInnerStage
                ref={isCurrent(stage.id) ? setCurrentStageNameEl : undefined}
                ellipsis={isCurrent(stage.id) && compressedView}
              >
                {!isCurrent(stage.id) && compressedView ? index + 1 : stage.name}
              </StyledInnerStage>
            )}
          </Flex>
        ))}
      </Flex>
      <Flex ref={popoverContentRef}>
        <Popover
          as={StyledStagePopover}
          show={showPopover && isSmallOrAbove}
          target={stagesRefs.current[activeStageIndex]}
          id='id'
          ref={setPopoverRef}
          arrow
          placement='bottom-start'
          onKeyDown={onKeyDownPopover}
          modifiers={[
            {
              name: 'flip',
              options: {
                fallbackPlacements: ['bottom-end', 'right', 'left']
              }
            }
          ]}
        >
          <StepsContainer currentStage={[activeStageObj]} />
          <Button variant='secondary' onClick={openModal}>
            {t('stage_see_full_lifecycle')}
          </Button>
        </Popover>
      </Flex>
    </>
  );
};

export default Stages;
