import {
  forwardRef,
  FunctionComponent,
  PropsWithoutRef,
  ReactNode,
  Ref,
  MouseEvent,
  useState,
  useEffect,
  useRef
} from 'react';
import styled, { css } from 'styled-components';

import {
  Flex,
  Tabs,
  Tab,
  ForwardProps,
  ExpandCollapse,
  defaultThemeProp,
  useConsolidatedRef,
  Icon,
  registerIcon,
  useI18n,
  useFocusWithin,
  Button,
  StyledIcon
} from '@pega/cosmos-react-core';
import * as arrowMicroLeftIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/arrow-micro-left.icon';

import CaseHeader, { CaseHeaderProps } from '../CaseHeader';

registerIcon(arrowMicroLeftIcon);

export interface CaseSummaryProps extends CaseHeaderProps {
  /** A region to display any summary information about the CaseView. */
  summary: ReactNode;
  /** An object handling the rendering and functionality of the Tabs in CaseSummary. */
  tabs: {
    items: Tab[];
    onClick: (id: string, event?: MouseEvent<HTMLAnchorElement | HTMLButtonElement>) => void;
    currentTabId: string;
  };
  /**
   * Forces rendering in the mobile layout, regardless of screen size.
   * @internal
   */
  forceMobile?: boolean;
  /**
   * Indicator for a desktop size.
   * @internal
   */
  desktop?: boolean;
  /** The fixed width string is needed for the expandable and collapsible header. */
  fixedWidth?: string;
  /** A boolean that toggles the collapsible feature of the case summary. */
  collapsible?: boolean;
  onBeforeCollapse?: () => void;
  onAfterCollapse?: () => void;
  onBeforeExpand?: () => void;
  onAfterExpand?: () => void;
  /** A callback that will return the animation state of the case summary. */
  onAnimationStateChange?: (state: AnimationState) => void;
  /** Ref for the header element inside the Case Summary */
  headerRef?: Ref<HTMLDivElement>;
  /** Ref for the wrapping element. */
  ref?: Ref<HTMLDivElement>;
}

type AnimationState = 'expanding' | 'expanded' | 'collapsing' | 'collapsed';

export const StyledCaseSummary = styled.div<{ expanded?: boolean; fixedWidth?: string }>(
  ({ theme, expanded, fixedWidth }) => {
    return css`
      position: relative;
      width: ${fixedWidth || 'auto'};
      max-width: ${fixedWidth ? 'none' : '100%'};
      height: 100%;

      ${expanded &&
      css`
        @media (min-width: ${theme.base.breakpoints.md}) {
          &:hover {
            &::after {
              content: '';
              position: absolute;
              top: 0;
              bottom: 0;
              inset-inline-start: 100%;
              width: 0.75rem;
            }
          }
        }
      `}
    `;
  }
);

export const StyledCaseSummaryInfo = styled.div`
  overflow: auto;
  flex: 1;
`;

const StyledExpandCollapse = styled.div<{ animationState: AnimationState }>(
  ({ animationState }) => {
    const expandingOrCollapsing = ['expanding', 'collapsing'].includes(animationState);
    return css`
      position: ${expandingOrCollapsing ? 'fixed' : 'relative'};
      top: ${expandingOrCollapsing ? 'var(--appshell-offset, 0)' : 0};
    `;
  }
);

const StyledExpandCollapseButton = styled(Button)<{
  expanded: boolean;
  headerHeight: number;
  animationState: AnimationState;
  visible: boolean;
}>(({ theme, expanded, headerHeight, animationState, visible }) => {
  return css`
    border-radius: ${expanded ? '1.5rem' : '0 1.5rem 1.5rem 0'};
    box-shadow: ${theme.base.shadow.low};
    border: none;
    opacity: ${visible ? '1' : '0'};
    position: ${animationState === 'collapsing' ? 'fixed' : 'absolute'};
    inset-inline-start: ${expanded ? 'calc(100% - 0.75rem)' : 0};
    top: calc(calc(${headerHeight}px / 2) - 0.75rem);
    z-index: 1;

    ${StyledIcon} {
      width: 1.5rem;
      height: 1.5rem;
      transform: rotate(${expanded ? '0' : '180deg'});
    }
  `;
});

StyledExpandCollapseButton.defaultProps = defaultThemeProp;

const CaseSummary: FunctionComponent<CaseSummaryProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<CaseSummaryProps>, ref: CaseSummaryProps['ref']) => {
    const {
      icon,
      heading,
      caseId,
      caseLink,
      parentCases,
      followed,
      onFollowedChange,
      actions,
      onEdit,
      summary,
      tabs,
      forceMobile,
      collapsible = false,
      desktop = true,
      fixedWidth,
      onBeforeCollapse,
      onAfterCollapse,
      onBeforeExpand,
      onAfterExpand,
      onAnimationStateChange,
      headerRef: caseSummaryHeaderRef,
      ...restProps
    } = props;

    const [showExpandCollapse, setShowExpandCollapse] = useState(false);
    const expandCollapseBtnRef = useRef<HTMLButtonElement>(null);
    const [expanded, setExpanded] = useState(true);
    const isMediumOrAbove = !forceMobile && desktop;
    const [animationState, setAnimationState] = useState('expanded');
    const [collapsedHeight, setCollapsedHeight] = useState(0);
    const caseSummaryRef = useConsolidatedRef(ref);
    const focusWithin = useFocusWithin<HTMLDivElement>([caseSummaryRef.current]);
    const t = useI18n();

    const setAnimStateAndUpdateParent = (state: AnimationState) => {
      setAnimationState(state);
      onAnimationStateChange?.(state);
    };

    const headerRef = useConsolidatedRef<HTMLDivElement>(caseSummaryHeaderRef);

    const showTabs = (isMediumOrAbove && tabs.items.length > 1) || !isMediumOrAbove;

    const caseHeaderProps = {
      actions,
      onEdit,
      isMediumOrAbove,
      caseId,
      parentCases,
      heading,
      caseLink,
      icon,
      followed,
      onFollowedChange,
      style: {
        maxHeight: ['expanding', 'collapsed', 'collapsing'].includes(animationState)
          ? `${collapsedHeight}px`
          : 'auto'
      }
    };

    useEffect(() => {
      if (['expanded'].includes(animationState)) {
        setCollapsedHeight(headerRef.current?.offsetHeight || 0);
      }
    }, [animationState, headerRef.current]);

    const expandOrCollapseLabel = expanded ? t('collapse') : t('expand');
    const expandOrCollapseA11y = expanded ? t('collapse_summary_panel') : t('expand_summary_panel');

    return (
      <Flex
        as={StyledCaseSummary}
        expanded={expanded}
        fixedWidth={fixedWidth}
        container={{ direction: 'column' }}
        item={{ shrink: 0 }}
        ref={caseSummaryRef}
        style={{
          marginTop: ['collapsing', 'expanding'].includes(animationState)
            ? `${collapsedHeight}px`
            : 0
        }}
        onMouseEnter={() => {
          setShowExpandCollapse(true);
        }}
        onMouseLeave={() => {
          setShowExpandCollapse(false);
        }}
        {...restProps}
      >
        {collapsible ? (
          <>
            <StyledExpandCollapseButton
              onClick={() => setExpanded(!expanded)}
              expanded={expanded}
              headerHeight={collapsedHeight}
              animationState={animationState}
              label={
                ['expanded', 'collapsed'].includes(animationState)
                  ? expandOrCollapseLabel
                  : undefined
              }
              aria-label={
                ['expanding', 'collapsing'].includes(animationState)
                  ? expandOrCollapseA11y
                  : undefined
              }
              visible={showExpandCollapse || !expanded || focusWithin}
              tabIndex={0}
              icon
              compact
              ref={expandCollapseBtnRef}
            >
              <Icon name='arrow-micro-left' />
            </StyledExpandCollapseButton>
            <ExpandCollapse
              dimension='width'
              min={isMediumOrAbove || !expanded ? fixedWidth : '100%'}
              max='100%'
              collapsed={expanded}
              onBeforeExpand={() => {
                setAnimStateAndUpdateParent('collapsing');
                onBeforeCollapse?.();
              }}
              onAfterExpand={() => {
                setAnimStateAndUpdateParent('collapsed');
                onAfterCollapse?.();
              }}
              onBeforeCollapse={() => {
                setAnimStateAndUpdateParent('expanding');
                onBeforeExpand?.();
              }}
              onAfterCollapse={() => {
                setAnimStateAndUpdateParent('expanded');
                onAfterExpand?.();
              }}
              as={StyledExpandCollapse}
              animationState={animationState}
              ref={headerRef}
            >
              <CaseHeader {...caseHeaderProps} />
            </ExpandCollapse>
          </>
        ) : (
          <CaseHeader {...caseHeaderProps} />
        )}
        <StyledCaseSummaryInfo>
          {isMediumOrAbove && summary}

          {showTabs && isMediumOrAbove && (
            <Tabs
              type='vertical'
              tabs={tabs.items}
              onTabClick={tabs.onClick}
              currentTabId={tabs.currentTabId}
            />
          )}
        </StyledCaseSummaryInfo>
      </Flex>
    );
  }
);

export default CaseSummary;
