import { ReactNode, FC, ReactElement, useMemo, MouseEvent } from 'react';
import styled, { css } from 'styled-components';

import {
  Card,
  CardHeader,
  CardContent,
  CardFooter,
  Flex,
  Text,
  SummaryItem,
  Count,
  Button,
  useI18n,
  ForwardProps,
  NoChildrenProp
} from '@pega/cosmos-react-core';

import TaskList, { TaskItem, TaskItemProps } from './TaskList';

export interface TasksProps extends NoChildrenProp {
  /** An array of TaskItemProps to generate the list.  */
  items: TaskItemProps[];
  /** Content to render when the list is empty. */
  emptyPlaceholder: ReactNode;
  /** Heading text to render at the top of the list. */
  headerText: string;
  /** A visual element i.e. (Avatar | Icon) to accompany the header text. */
  headerVisual?: ReactElement;
  /** A region for supplemental task list info or controls. */
  headerSecondary?: ReactNode;
  /** Integer representing the total count of tasks. */
  count?: number | null;
  /** Callback for when to show more/less button is clicked. Lists greater than 3 items will display the show toggle button in its footer. */
  onExpandToggle?: () => void;
}

const StyledTasks = styled(Card)<{ openItem?: boolean }>(({ theme, openItem }) => {
  return openItem
    ? css`
        box-shadow: ${theme.base.shadow.low};
      `
    : null;
});

const Tasks: FC<ForwardProps & TasksProps> = (props: TasksProps) => {
  const {
    items = [],
    headerText,
    headerVisual,
    headerSecondary,
    count,
    onExpandToggle,
    emptyPlaceholder,
    ...restProps
  } = props;
  const openItem = items.find(item => item.content);
  const t = useI18n();

  const hasFooter = useMemo(() => {
    return !openItem && typeof count === 'number' && count > 3;
  }, [openItem, count]);

  const TaskItems = useMemo(() => {
    return openItem ? <TaskItem {...openItem} /> : <TaskList items={items} hasFooter={hasFooter} />;
  }, [items, openItem]);

  return (
    <StyledTasks {...restProps} openItem={!!openItem}>
      {!openItem && (
        <CardHeader>
          <SummaryItem
            visual={headerVisual}
            primary={
              <Flex container={{ alignItems: 'center', gap: 1 }}>
                <Text variant='h2'>{headerText}</Text>
                <Count>{count ?? null}</Count>
              </Flex>
            }
            secondary={headerSecondary}
          />
        </CardHeader>
      )}
      <CardContent>{items.length > 0 ? TaskItems : emptyPlaceholder}</CardContent>

      {hasFooter && (
        <CardFooter justify='center'>
          <Button
            variant='link'
            aria-label={t(!!count && count > items.length ? 'show_more' : 'show_less', ['tasks'])}
            onClick={(e: MouseEvent<HTMLButtonElement>) => {
              onExpandToggle?.();
              if (!!count && count <= items.length) {
                const { currentTarget } = e;
                requestAnimationFrame(() => {
                  if (currentTarget.getBoundingClientRect().top < 0) currentTarget.scrollIntoView();
                });
              }
            }}
          >
            {t(!!count && count > items.length ? 'show_more' : 'show_less')}
          </Button>
        </CardFooter>
      )}
    </StyledTasks>
  );
};

export default Tasks;
