import { FunctionComponent } from 'react';
import styled, { css } from 'styled-components';

import { Card, Count, Flex, Icon, ForwardProps, useI18n } from '@pega/cosmos-react-core';

export interface UtilitiesSummaryProps {
  items: { iconName: string; count?: number }[];
}

export const StyledUtilitiesSummary = styled.div(({ theme }) => {
  return css`
    width: 2.75rem;
    padding: ${theme.base.spacing} 0;
  `;
});

const UtilitiesSummary: FunctionComponent<UtilitiesSummaryProps & ForwardProps> = ({
  items = [],
  ...restProps
}: UtilitiesSummaryProps) => {
  const t = useI18n();
  return items.length ? (
    <Card
      {...restProps}
      interactive
      tabIndex={0}
      role='button'
      container={{ direction: 'column', alignItems: 'center', gap: 3 }}
      aria-label={`${t('utilities_summary')}. ${t('expand_utilities_panel')}`}
      as={StyledUtilitiesSummary}
    >
      {items.map(item => {
        return (
          <Flex
            container={{
              direction: 'column',
              alignItems: 'center',
              gap: 0.5
            }}
            item={{ grow: 1 }}
            key={item.iconName}
          >
            <Icon name={item.iconName} />
            {item.count !== undefined && <Count variant='default'>{item.count}</Count>}
          </Flex>
        );
      })}
    </Card>
  ) : null;
};

export default UtilitiesSummary;
