import { forwardRef, FunctionComponent, PropsWithoutRef, ReactNode, Ref } from 'react';
import styled, { css } from 'styled-components';

import {
  ForwardProps,
  SummaryItem,
  StyledSummaryListItem,
  Flex,
  defaultThemeProp
} from '@pega/cosmos-react-core';

export interface TaskItemProps {
  /** Name of the task displayed in the list. */
  name: NonNullable<ReactNode>;
  /** Avatar element to represent the task's assignee. */
  avatar?: ReactNode;
  /** MetaList element with info related to the task. */
  meta?: ReactNode;
  /** Region for task level actions such as opening or dismissing. */
  actions?: ReactNode;
  /** Content for the task when open. */
  content?: ReactNode;
  [key: string]: unknown;
}

export interface TaskListProps {
  items: TaskItemProps[];
  hasFooter?: boolean;
  /** Ref for the wrapping element. */
  ref?: Ref<HTMLElement>;
}

const StyledTaskList = styled.ul<{ hasFooter: boolean }>(({ theme, hasFooter }) => {
  return css`
    ${hasFooter &&
    css`
      > li:last-child {
        border-bottom: 0.0625rem solid ${theme.base.palette['border-line']};
      }
    `}
  `;
});

StyledTaskList.defaultProps = defaultThemeProp;

export const TaskItem = ({ name, avatar, meta, actions, content, ...restProps }: TaskItemProps) => {
  const summary = (
    <SummaryItem
      visual={avatar}
      primary={name}
      secondary={meta}
      actions={content ? null : actions}
    />
  );
  return (
    <Flex
      container={{ direction: 'column', gap: 2 }}
      item={{ grow: 1 }}
      as={StyledSummaryListItem}
      forwardedAs='li'
      {...restProps}
    >
      {content ? (
        <>
          {summary}
          {content}
        </>
      ) : (
        summary
      )}
    </Flex>
  );
};

const TaskList: FunctionComponent<TaskListProps & ForwardProps> = forwardRef(
  ({ items, hasFooter = false }: PropsWithoutRef<TaskListProps>, ref) => {
    return (
      <Flex container={{ direction: 'column' }} as={StyledTaskList} hasFooter={hasFooter} ref={ref}>
        {items.map((item, i) => (
          <TaskItem {...item} key={`${i + 1}`} />
        ))}
      </Flex>
    );
  }
);

export default TaskList;
