import {
  ReactNode,
  Fragment,
  FunctionComponent,
  useState,
  useLayoutEffect,
  useRef,
  useMemo
} from 'react';
import styled from 'styled-components';

import { defaultThemeProp } from '../../theme';
import { BaseProps, ForwardProps, NoChildrenProp } from '../../types';
import Button from '../Button';
import ExpandCollapse from '../ExpandCollapse';
import Grid from '../Grid';
import { StyledLabel } from '../Label';
import { useI18n } from '../../hooks';
import { windowIsAvailable } from '../../utils';

export interface FieldValueListProps extends BaseProps, NoChildrenProp {
  /** The set of field values to render in the list. */
  fields: {
    id?: string;
    name: string;
    value?: ReactNode;
  }[];
  /**
   * Determines if the field and values should be displayed on the same line, or stacked with the field above the value.
   * @default "inline"
   */
  variant?: 'inline' | 'stacked';
}

export const StyledFieldName = styled.dt`
  word-break: break-word;
`;

export const StyledFieldValue = styled.dd`
  word-break: break-word;
`;

export const StyledFieldValueList = styled.dl`
  width: 100%;
`;

StyledFieldValueList.defaultProps = defaultThemeProp;

const DynamicHeightValue = ({ children }: { children: ReactNode }): JSX.Element => {
  const [collapsed, setCollapsed] = useState(true);
  const [exceedsMaxHeight, setExceedsMaxHeight] = useState(false);
  const textRef = useRef<HTMLSpanElement>(null);
  const t = useI18n();

  const maxTextHeight = useMemo(() => {
    if (!windowIsAvailable) return Infinity;

    const computedLineHeight =
      Number.parseInt(
        window.getComputedStyle(document.documentElement).getPropertyValue('font-size'),
        10
      ) * 1.25;

    return 3 * computedLineHeight;
  }, []);

  useLayoutEffect(() => {
    if (!textRef.current) return;

    const observer = new ResizeObserver(entries => {
      const exceedsHeight = entries.some(({ target, contentRect }) => {
        if (target !== textRef.current) return;

        return contentRect.height > maxTextHeight;
      });

      setExceedsMaxHeight(exceedsHeight);
    });

    observer.observe(textRef.current);

    return () => {
      observer.disconnect();
    };
  }, [textRef.current, maxTextHeight]);

  return (
    <>
      <ExpandCollapse
        collapsed={collapsed}
        min={exceedsMaxHeight ? `${maxTextHeight}px` : 'max-content'}
      >
        <Grid ref={textRef}>
          <span>{children}</span>
        </Grid>
      </ExpandCollapse>
      {exceedsMaxHeight && (
        <Button variant='link' onClick={() => setCollapsed(current => !current)}>
          {collapsed ? t('show_more') : t('show_less')}
        </Button>
      )}
    </>
  );
};

const FieldValueList: FunctionComponent<FieldValueListProps & ForwardProps> = ({
  fields,
  variant = 'inline',
  ...restProps
}: FieldValueListProps) => {
  const Wrapper = variant === 'inline' ? Fragment : 'div';

  return (
    <Grid
      {...restProps}
      container={{
        cols: variant === 'inline' ? '16ch auto' : 'minmax(0, 1fr)',
        colGap: 2,
        rowGap: 1
      }}
      as={StyledFieldValueList}
    >
      {fields.map(({ id, name, value }) => {
        const diplayValue =
          typeof value === 'string' ? <DynamicHeightValue>{value}</DynamicHeightValue> : value;
        return (
          <Wrapper key={id ?? name}>
            <StyledLabel as={StyledFieldName}>{name}</StyledLabel>
            <StyledFieldValue>
              {value ? diplayValue : <span aria-hidden='true'>&ndash;&ndash;</span>}
            </StyledFieldValue>
          </Wrapper>
        );
      })}
    </Grid>
  );
};

export default FieldValueList;
