import { FunctionComponent, PropsWithoutRef } from 'react';

import { Flex, Icon, registerIcon, DateTimeDisplay, Text } from '@pega/cosmos-react-core';
import * as checkIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/check.icon';

import Steps from './Steps';
import { StagesProps } from './Stages.types';
import { StyledDateTimeDisplay, StyledInnerStep, StyledSteps } from './Stages.styles';

registerIcon(checkIcon);

export interface StepsContainerProps {
  currentStage: StagesProps['stages'];
}

const StepsContainer: FunctionComponent<StepsContainerProps> = ({
  currentStage
}: PropsWithoutRef<StepsContainerProps>) => {
  return (
    <Flex as={StyledSteps} container={{ direction: 'column' }} item={{ grow: 1 }}>
      <StyledInnerStep complete={currentStage[0]?.complete}>
        {currentStage[0]?.complete && <Icon name='check' />}
        <Text variant='h2'>{currentStage[0]?.name}</Text>
      </StyledInnerStep>
      {currentStage[0]?.date && (
        <DateTimeDisplay
          as={StyledDateTimeDisplay}
          value={currentStage[0]?.date}
          variant='relative'
          format='short'
        />
      )}

      <Steps steps={currentStage[0].steps} />
    </Flex>
  );
};

export default StepsContainer;
